import React from 'react';

let textos = []


for (var i = 0; i < 100; i++) {

    if ((i % 15) === 0) {
        textos.push(`FizzBuzz`);
    }
    else if ((i % 3) === 0 && i !== 0) {
        textos.push("Fizz");
    }
    else if ((i % 5) === 0 && i !== 0) {
        textos.push("Buzz");
    }
    else {
        textos.push(i)
    }
}
const FizzBuzz = textos.map(item => item)
console.log(FizzBuzz)


const LoginPage = () => {
    const FizzBuzz = textos.map((item, id) => (
        <li key={id}>
            {item}
        </li>
    ))
    return (<div>
        <form className="login">
            <label htmlFor="">Podaj login  <input type="text" autoComplete="username"></input></label>
            <label htmlFor="">Podaj hasło  <input type="password" autoComplete="current-password"></input></label>
            <button className="login">Zaloguj</button>
        </form>

        <ul className="fizz">
            {/* {FizzBuzz} */}
        </ul>
    </div>);
}

export default LoginPage;