import React from 'react';

const Article = ({ title, author, text, id }) => {


    if (id !== 1) {
        const styles = {
            marginTop: 40
        }
        return (
            <article style={styles}>
                <h3 style={{
                    marginBottom: 3,
                    textTransform: "uppercase",
                }}>{title}</h3>
                <span style={{
                    display: "block",
                    marginBottom: 10,
                    fontSize: 12,
                }}>{author}</span>
                <p style={{
                    fontSize: 15,
                }}>{text}</p>
            </article>
        );
    } else {
        const styles = {
            marginTop: 0
        }
        return (
            <article style={styles}>
                <h3 style={{
                    marginBottom: 3,
                    textTransform: "uppercase",
                }}>{title}</h3>
                <span style={{
                    display: "block",
                    marginBottom: 10,
                    fontSize: 12,
                }}>{author}</span>
                <p style={{
                    fontSize: 15,
                }}>{text}</p>
            </article>
        );
    }


}

export default Article;